=\n      Slider css start\n==========================  **/\n\n\n/*========= Owl-carousel css start ============*/\n\n.owl-theme .owl-nav [class*='owl-']:hover {\n    background: $primary-color;\n}\n\n.owl-theme .owl-dots .owl-dot {\n    &.active span,\n    &:hover span {\n        background: $primary-color;\n    }\n}\n\n\n/*========= Owl-carousel css end ============*/\n\n\n/**  =====================\n      Animation css start\n==========================  **/\n\n.animation-image img {\n    margin: 10px auto 20px;\n    cursor: pointer;\n}\n\n\n/**====== Animation css end ======**/\n\n\n/**  =====================\n      Tree-view css start\n==========================  **/\n\n.card-block.tree-view ul {\n    display: inherit;\n    li {\n        display: inherit;\n        float: none;\n    }\n}\n\n.jstree-default>.jstree-container-ul .jstree-loading>.jstree-ocl {\n    background: url(\"./../../assets/images/treeview/throbber.gif\") center center no-repeat;\n}\n\n\n/**====== Tree-view css end ======**/\n\n\n/**  =====================\n      File Upload css start\n==========================  **/\n\n.jFiler-theme-default {\n    .jFiler-input-button {\n        background-color: $primary-color;\n        background-image: none;\n        color: #fff;\n    }\n    .jFiler-input {\n        width: 100%;\n    }\n}\n\n.jFiler-input-dragDrop {\n    width: 100%;\n    background-color: #fafafa;\n}\n\n.jFiler-items-grid .jFiler-item .jFiler-item-container .jFiler-item-info {\n    position: absolute;\n    bottom: -10%;\n    left: 0;\n    width: 100%;\n    color: #fff;\n    padding: 6px 10px;\n    z-index: 9;\n    opacity: 0;\n    -webkit-transition: all 0.12s;\n    -moz-transition: all 0.12s;\n    transition: all 0.12s;\n}\n\n.jFiler-input-choose-btn {\n    border: 1px solid $primary-color;\n    padding: 10px 25px;\n    background: $primary-color;\n    color: #fff !important;\n    &:hover {\n        background: #fff;\n        border: 1px solid $primary-color;\n        color: #fff !important;\n    }\n}\n\n.card .jFiler-item-assets ul li {\n    margin-right: 0;\n}\n\n\n/**====== File Upload css end ======**/\n\n\n/**  =====================\n      Cropper css start\n==========================  **/\n\n.img-container,\n.img-preview {\n    background-color: #f7f7f7;\n    width: 100%;\n    text-align: center;\n}\n\n.img-container {\n    min-height: 200px;\n    max-height: 516px;\n    margin-bottom: 20px;\n}\n\n@media (min-width: 768px) {\n    .img-container {\n        min-height: 516px;\n    }\n}\n\n.img-container>img {\n    max-width: 100%;\n}\n\n.docs-preview {\n    margin-right: -15px;\n}\n\n.img-preview {\n    float: left;\n    margin-right: 10px;\n    margin-bottom: 10px;\n    overflow: hidden;\n    >img {\n        max-width: 100%;\n    }\n}\n\n.preview-lg {\n    width: 100% !important;\n}\n\n.preview-md {\n    width: 139px;\n    height: 78px;\n}\n\n.preview-sm {\n    width: 69px;\n    height: 39px;\n}\n\n.preview-xs {\n    width: 35px;\n    height: 20px;\n    margin-right: 0;\n}\n\n.docs-data>.input-group {\n    margin-bottom: 10px;\n    >label {\n        min-width: 80px;\n    }\n    >span {\n        min-width: 50px;\n    }\n}\n\n.docs-buttons {\n    >.btn,\n    >.form-control,\n    >.btn-group {\n        margin-right: 5px;\n    }\n}\n\n.docs-toggles {\n    >.btn,\n    >.dropdown,\n    >.btn-group {\n        margin-bottom: 10px;\n    }\n}\n\n.docs-tooltip {\n    display: block;\n    margin: -6px -12px;\n    padding: 6px 12px;\n    >.icon {\n        margin: 0 -3px;\n        vertical-align: top;\n    }\n}\n\n.tooltip-inner {\n    white-space: normal;\n}\n\n.btn-upload .tooltip-inner {\n    white-space: nowrap;\n}\n\n@media (max-width: 400px) {\n    .btn-group-crop {\n        margin-right: -15px !important;\n        >.btn {\n            padding-left: 5px;\n            padding-right: 5px;\n        }\n        .docs-tooltip {\n            margin-left: -5px;\n            margin-right: -5px;\n            padding-left: 5px;\n            padding-right: 5px;\n        }\n    }\n}\n\n.docs-options .dropdown-menu {\n    width: 100%;\n    >li {\n        padding: 3px 20px;\n        font-size: 14px;\n        &:hover {\n            background-color: #f7f7f7;\n        }\n        >label {\n            display: block;\n        }\n    }\n}\n\n.docs-cropped .modal-body {\n    text-align: center;\n    >img,\n    >canvas {\n        max-width: 100%;\n    }\n}\n\n.card-block .docs-options .dropdown-menu {\n    top: inherit;\n}\n\nlabel.btn-upload {\n    height: 40px;\n}\n\n\n/**====== Cropper css end ======**/\n\n\n/**  =====================\n      Accordion css start\n==========================  **/\n\n.accordion-msg {\n    display: block;\n    color: #222222;\n    padding: 14px 20px;\n    border-top: 1px solid #ddd;\n    font-weight: 500;\n    cursor: pointer;\n}\n\n.accordion-msg:focus,\n.accordion-msg:hover {\n    text-decoration: none;\n    outline: none;\n}\n\n.faq-accordion .accordion-desc {\n    padding: 20px;\n}\n\n.accordion-desc {\n    color: #222222;\n    padding: 0 20px 20px;\n}\n\n#color-accordion .accordion-desc {\n    margin-top: 14px;\n}\n\n.ui-accordion-header-icon {\n    float: right;\n    font-size: 20px;\n}\n\n.accordion-title {\n    margin-bottom: 0;\n}\n\n.accordion-block {\n    padding: 0;\n    p {\n        margin-bottom: 0;\n    }\n}\n\n.color-accordion-block a {\n    &.ui-state-active,\n    &:focus,\n    &:hover {\n        color: #fff;\n        background: #4680ff;\n    }\n}\n\na.bg-default:focus,\na.bg-default:hover {\n    background-color: lighten($default-color, 10%) !important;\n    color: #fff;\n}\n\na.bg-primary:focus,\na.bg-primary:hover {\n    background-color: lighten($primary-color, 10%) !important;\n    color: #fff;\n}\n\na.bg-success:focus,\na.bg-success:hover {\n    background-color: lighten($success-color, 10%) !important;\n    color: #fff;\n}\n\na.bg-info:focus,\na.bg-info:hover {\n    background-color: lighten($info-color, 10%) !important;\n    color: #fff;\n}\n\na.bg-warning:focus,\na.bg-warning:hover {\n    background-color: lighten($warning-color, 10%) !important;\n    color: #fff;\n}\n\na.bg-danger:focus,\na.bg-danger:hover {\n    background-color: lighten($danger-color, 10%) !important;\n    color: #fff;\n}\n\n\n/**====== Accordion css end ======**/\n\n\n/**  =====================\n      Tabs css start\n==========================  **/\n\n.tab-list p {\n    padding: 10px;\n}\n\n.tab-with-img i {\n    position: absolute;\n    padding: 5px;\n}\n\n.tab-icon {\n    margin-bottom: 30px;\n    i {\n        padding-right: 10px;\n    }\n}\n\n.tab-below {\n    border-top: 1px solid #ddd;\n    border-bottom: none;\n    &.nav-tabs .nav-link.active {\n        border-color: transparent #ddd #ddd #ddd;\n    }\n    .nav-item {\n        margin-top: -2px;\n    }\n    &.nav-tabs .nav-link {\n        border-bottom-right-radius: 0.25rem;\n        border-bottom-left-radius: 0.25rem;\n        border-top-right-radius: 0;\n        border-top-left-radius: 0;\n    }\n}\n\n.card-header~.tab-icon .tab-with-img .sub-title i {\n    right: 10px;\n}\n\n.tab-with-img .nav-link {\n    position: relative;\n}\n\n.tabs-left,\n.tabs-right {\n    min-width: 120px;\n    vertical-align: top;\n    width: 150px;\n}\n\n.tabs-left,\n.tabs-left-content,\n.tabs-right,\n.tabs-right-content {\n    display: table-cell;\n}\n\n.nav-tabs.tabs-left .slide {\n    height: 35px;\n    width: 4px;\n    bottom: 15px;\n}\n\n.nav-tabs.tabs-right .slide {\n    height: 35px;\n    width: 4px;\n    bottom: 15px;\n    right: 0;\n}\n\n.product-edit {\n    .md-tabs {\n        .nav-item {\n            a {\n                padding: 0 0 10px !important;\n                color: $inverse-color;\n                .f-20 {\n                    display: inline-block;\n                    margin-right: 10px;\n                }\n            }\n        }\n    }\n}\n\n.md-tabs.tabs-left .nav-item,\n.md-tabs.tabs-right .nav-item,\n.tabs-left .nav-item,\n.tabs-right .nav-item {\n    width: 100%;\n    position: relative;\n}\n\n.md-tabs {\