\n\n/**====== Tabs css end ======**/\n\n\n/**  =====================\n      Table css start\n==========================  **/\n\n.table-primary,\n.table-primary>td,\n.table-primary>th {\n    background-color: #4680FE;\n}\n\n.table-responsive {\n    display: inline-block;\n    width: 100%;\n    overflow-x: auto;\n}\n\n.table.table-xl {\n    td,\n    th {\n        padding: 1.25rem 2rem;\n    }\n}\n\n.table.table-lg {\n    td,\n    th {\n        padding: 0.9rem 2rem;\n    }\n}\n\n.table.table-de {\n    td,\n    th {\n        padding: 0.75rem 2rem;\n    }\n}\n\n.table.table-sm {\n    td,\n    th {\n        padding: 0.6rem 2rem;\n    }\n}\n\n.table.table-xs {\n    td,\n    th {\n        padding: 0.4rem 2rem;\n    }\n}\n\n.table-columned {\n    >tbody>tr {\n        >td:first-child,\n        >th:first-child {\n            border-left: 0;\n        }\n    }\n    >tfoot>tr {\n        >td:first-child,\n        >th:first-child {\n            border-left: 0;\n        }\n    }\n    >tbody>tr {\n        >td,\n        >th {\n            border: 0;\n            border-left: 1px solid #ddd;\n        }\n    }\n    >tfoot>tr {\n        >td,\n        >th {\n            border: 0;\n            border-left: 1px solid #ddd;\n        }\n    }\n}\n\n.table-border-style {\n    padding: 0;\n    .table {\n        margin-bottom: 0;\n    }\n}\n\n.table>thead>tr>th {\n    border-bottom-color: #ccc;\n}\n\n.table-borderless tbody tr {\n    td,\n    th {\n        border: 0;\n    }\n}\n\n.table-bordered>thead>tr.border-solid {\n    >td,\n    >th {\n        border-bottom-width: 2px;\n    }\n    &:first-child {\n        >td,\n        th {\n            border-bottom-width: 2px;\n        }\n    }\n}\n\n.table-bordered>thead>tr.border-double {\n    >td,\n    >th {\n        border-bottom-width: 3px;\n        border-bottom-style: double;\n    }\n    &:first-child {\n        >td,\n        th {\n            border-bottom-width: 3px;\n            border-bottom-style: double;\n        }\n    }\n}\n\n\n/**====== T