or css end  ======= */\n\n\n/*======= Button-color css starts  ======= */\n\n$btn-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo;\n$btn-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $btn-name {\n    $i: index($btn-name, $var);\n    .color-#{$var} {\n        background-color: nth($btn-color, $i);\n    }\n}\n\n\n/*======= Button-color css ends  ======= */\n\n\n/*======= Loader-color css starts  ======= */\n\n$loader-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo;\n$loader-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $loader-name {\n    $i: index($loader-name, $var);\n    .loader-#{$var} {\n        background-color: nth($loader-color, $i) !important;\n    }\n}\n\n\n/*======= Loader-color css ends  ======= */\n\n\n/*======= Nestable-color css starts  ======= */\n\n$nestable-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo;\n$nestable-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $nestable-name {\n    $i: index($nestable-name, $var);\n    .nestable-#{$var} {\n        background-color: nth($nestable-color, $i) !important;\n        border-color: nth($nestable-color, $i);\n        color: #fff !important;\n    }\n}\n\n\n/*======= Nestable-color css ends  ======= */\n\n\n/*======= Table-Border-Bottom-color css starts  ======= */\n\n$border-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo;\n$border-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $border-name {\n    $i: index($border-name, $var);\n    table thead .border-bottom-#{$var} th,\n    table tbody .border-bottom-#{$var} th,\n    table tbody .border-bottom-#{$var} td {\n        border-bottom: 1px solid nth($border-color, $i);\n    }\n}\n\n\n/*======= Table-Border-Bottom-color css ends  ======= */\n\n\n/*======= Table styling css starts  ======= */\n\n$table-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo;\n$table-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $table-name {\n    $i: index($table-name, $var);\n    .table-styling .table-#{$var},\n    .table-styling.table-#{$var} {\n        background-color: nth($border-color, $i);\n        color: #fff;\n        border: 3px solid nth($border-color, $i);\n        thead {\n            background-color: darken(nth($border-color, $i), 10%);\n            border: 3px solid darken(nth($border-color, $i), 10%);\n        }\n    }\n}\n\n\n/*======= Table styling css ends  ======= */\n\n\n/*======= Toolbar css starts  ======= */\n\n$light:#bdc3c7;\n$dark:#34495e;\n$toolbar-name:primary,\nwarning,\nlight,\ndanger,\nsuccess,\ndark,\ninfo;\n$toolbar-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $toolbar-name {\n    $i: index($toolbar-name, $var);\n    .toolbar-#{$var} .tool-item {\n        background: nth($toolbar-color, $i) !important;\n    }\n    .toolbar-#{$var} .tool-item.selected,\n    .toolbar-#{$var} .tool-item:hover {\n        background: darken(nth($toolbar-color, $i), 10%) !important;\n    }\n    .toolbar-#{$var}.tool-top .arrow {\n        border-color: nth($toolbar-color, $i) transparent transparent;\n    }\n    .toolbar-#{$var}.tool-bottom .arrow {\n        border-color: transparent transparent nth($toolbar-color, $i);\n    }\n    .toolbar-#{$var}.tool-left .arrow {\n        border-color: transparent transparent transparent nth($toolbar-color, $i);\n    }\n    .toolbar-#{$var}.tool-right .arrow {\n        border-color: transparent nth($toolbar-color, $i) transparent transparent;\n    }\n    .btn-toolbar-#{$var}.pressed {\n        background-color: nth($toolbar-color, $i);\n    }\n}\n\n\n/*======= Toolbar css ends  ======= */\n\n\n/*======= Card-Border-Top-color css starts  ======= */\n\n$card-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo;\n$card-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $card-name {\n    $i: index($card-name, $var);\n    .card-border-#{$var} {\n        border-top: 4px solid nth($border-color, $i);\n    }\n}\n\n\n/*======= Card-Border-Top-color css ends  ======= */\n\n\n/*======= Panels-wells-color css starts  ======= */\n\n$panel-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo;\n$panel-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color;\n@each $var in $panel-name {\n    $i: index($panel-name, $var);\n    .panels-wells .panel-#{$var} {\n        border-color: nth($border-color, $i);\n    }\n}\n\n\n/*======= Panels-wells-color css ends  ======= */\n\n\n/*======= All-Borders-color css starts  ======= */\n\n$b-name:primary,\nwarning,\ndefault,\ndanger,\nsuccess,\ninverse,\ninfo,\ntheme;\n$b-color:$primary-color,\n$warning-color,\n$default-color,\n$danger-color,\n$success-color,\n$inverse-color,\n$info-color,\n$theme-border;\n@each $var in $b-name {\n    $i: index($b-name, $var);\n    .b-t-#{$var} {\n        border-top: 1px solid nth($b-color, $i);\n    }\n    .b-b-#{$var} {\n        border-bottom: 1px solid nth($b-color, $i);\n    }\n    .b-l-#{$var} {\n        border-left: 1px solid nth($b-color, $i);\n    }\n    .b-r-#{$var} {\n        border-right: 1px solid nth($b-color, $i);\n    }\n    .b-#{$var} {\n        border: 1px solid nth($b-color, $i);\n    }\n}\n\n\n/*======= All-Borders-color css ends  ======= */\n/*======= All-color css start  ======= */\n@each $value in $social-name {\n    $i: index($social-name, $value);\n\n    .bg-#{$value} {\n        background:nth($social-color,$i);\n    }\n\n    .text-#{$value} {\n        color: nth($social-color,$i);\n    }\n}\n@each $value in $color-name {\n    $i: index($color-name, $value);\n\n    .bg-#{$value} {\n        background:nth($color-color,$i),;\n    }\n\n    .text-#{$value} {\n        color: nth($color-color,$i);\n    }\n}\n/*======= All-color css ends  ======= */\n\n\n/**====== Generic-class css end ======**/\n\n\n\n","$menu-title: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f,#000,#999,#fff;\n$header-theme: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f;\n$box-bg-color: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f;\n$left-navbar-theme: #37474f;\n$left-navbar-light-theme: #fff;\n$active-item: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f,#11c788,#FF5252,#ff784f,#38a9f9,#038f9e,#39ADB5;\n$icon-color: #448aff,#b71c1c,#4CAF50,#d84315,#4527a0,#37474f,#7C4DFF,#FF5370;\n\n.pcoded {\n    &[nav-type=\"st1\"] {\n        .pcoded-item.pcoded-left-item {\n            $i: 1;\n            @for $k from 1 through 3 {\n                @each $value in $icon-color {\n                    > li {\n                        &:nth-child(#{$i}) {\n                            > a > .pcoded-micon {\n                                color: $value;\n                            }\n                        }\n                    }\n                    $i: $i+1;\n                }\n            }\n        }\n    }\n\n    &[nav-type=\"st2\"] {\n        .pcoded-item.pcoded-left-item {\n            > li {\n                > a > .pcoded-micon {\n                    color: $inverse-color;\n                }\n            }\n        }\n    }\n\n    &[nav-type=\"st1\"],\n    &[nav-type=\"st2\"] {\n        .pcoded-item.pcoded-left-item {\n            > li {\n                > a > .pcoded-micon {\n                    color: $inverse-color;\n\n                    b {\n                        display: none;\n                    }\n                }\n            }\n        }\n    }\n}\n// icon color end\n/**  =====================\n      Left-side-menu css start\n==========================  **/\n.main-menu {\n    float: left;\n    width: 235px;\n    z-index: 99;\n    height: 100% !important;\n    @media only screen and (max-width: 992px) {\n        height: 100% !important;\n    }\n\n    .main-menu-header {\n        padding: 15px 0;\n        background-image: url(\"./../../../assets/images/user-bg.jpg\");\n        background-size: cover;\n        text-align: center;\n        position: relative;\n        img{\n            position: relative;\n            width: 60px;\n        }\n        .user-details {\n            display: block;\n            margin-top: 10px;\n            position: relative;\n\n            #more-details {\n                cursor: pointer;\n            }\n\n            span {\n                color: #fff;\n                display: block;\n                font-size: 14px;\n                text-transform: capitalize;\n\n                i {\n                    font-size: 10px;\n                    margin-left: 10px;\n                }\n            }\n        }\n        &:before{\n            content: \"\";\n            background: rgba(0,0,0,0.2);\n            width: 100%;\n            height: 100%;\n            position: absolute;\n            top: 0;\n            left: 0;\n            z-index: 0;\n        }\n    }\n\n    .main-menu-content {\n        .more-details {\n            display: none;\n\n            a {\n                padding: 10px 25px;\n                display: block;\n                color: #000;\n                transition: all ease-in 0.3s;\n\n                &:hover {\n                    color: $primary-color;\n                }\n\n                i {\n                    margin-right: 10px;\n                }\n            }\n        }\n\n        .menu-search {\n            padding: 20px 20px 10px;\n        }\n\n        li {\n            position: relative;\n        }\n    }\n}\n\n.pcoded[theme-layout=\"vertical\"][vertical-nav-type=\"collapsed\"] {\n    .main-menu-content,\n    .main-menu-header {\n        display: none;\n    }\n\n    .main-menu {\n        width: 70px;\n    }\n}\n\n.sidebar_toggle {\n    a {\n        margin: -5px;\n        line-height: 1;\n    }\n}\n/**  =====================\n      Main-header css start\n==========================  **/\n\n.header-navbar {\n    min-height: 56px;\n    padding: 0;\n\n    .navbar-wrapper {\n        .navbar-logo {\n            position: relative;\n            align-items: center;\n            display: flex;\n            float: left;\n            height: 56px;\n            text-align: center;\n            text-transform: uppercase;\n            width: 235px;\n            padding: 10px;\n            @media only screen and (max-width: 992px) {\n                justify-content: center;\n            }\n\n            h5 {\n                margin-bottom: 0;\n            }\n\n            .mobile-menu {\n                position: absolute;\n                right: 0;\n                font-size: 16px;\n                line-height: 3.5;\n                width: 40px;\n            }\n            @media only screen and(max-width: 768px) {\n                .mobile-search {\n                    display: none !important;\n                }\n            }\n\n            .mobile-options,\n            .mobile-search {\n                display: none;\n            }\n        }\n\n        .navbar-container {\n            .nav-left {\n                float: left;\n                margin-bottom: 0;\n\n                li {\n                    float: left;\n                    line-height: 3.5;\n                    // padding: 0 10px; removing space for breadcrumb by shubhangi\n                    padding: 0 2px;\n                    position: relative;\n                    @media only screen and (max-width: 575px) {\n                        padding: 0 5px;\n                    }\n\n                    .sidebar_toggle a {\n                        display: none;\n                    }\n\n                    > a {\n                        font-size: 16px;\n                        display: block;\n\n                        > i {\n                            &.flag-icon {\n                                border-radius: 50%;\n                                width: 20px;\n                                height: 20px;\n                            }\n                        }\n                    }\n                }\n\n                a {\n                    //space change by shubhangi\n                    padding: 0 0.2rem;\n                }\n\n                .mega-menu-top {\n                    i {\n                        font-size: 10px;\n                        margin-left: 10px;\n                    }\n                }\n            }\n\n            .nav-right {\n                float: right;\n                @extend .nav-left;\n\n                .user-profile {\n                    img {\n                        margin-right: 10px;\n                        height: 40px;\n                        width: 40px;\n                    }\n\n                    i {\n                        font-size: 10px;\n                        margin-left: 10px;\n                    }\n                }\n\n                > .header-notification:nth-child(2) {\n                    .show-notification {\n                        li:first-child:hover {\n                            background-color: #fff;\n                        }\n                    }\n                }\n            }\n\n            .header-notification {\n                perspective: 1000px;\n                z-index: 99;\n                position: relative;\n\n                .show-notification {\n                    background: #fff;\n                    box-shadow: 0 0 35px 0 rgba(0, 0, 0, 0.25);\n                    border-radius: 5px;\n                    opacity: 1;\n                    position: absolute;\n                    right: 0;\n                    visibility: visible;\n                    width: 24rem;\n                    display: none;\n                    transform: translateY(20px);\n\n                    a {\n                        color: $theme-font-color;\n                    }\n\n                    li {\n                        border-top: 1px solid $border-color;\n                        line-height: initial;\n                        padding: 1em 20px;\n                        width: 100%;\n                        transition: all 0.3s ease-in-out;\n\n                        &:first-child {\n                            padding: 10px 20px;\n\n                            h6 {\n                                display: inline-block;\n                                font-size: 14px;\n                                font-weight: 500;\n                                margin-bottom: 0;\n                            }\n\n                            label {\n                                float: right;\n                            }\n                        }\n\n                        img {\n                            width: 40px;\n                            margin-right: 10px;\n                            align-self: flex-start !important;\n                        }\n\n                        &:hover {\n                            background-color: #f1f1f1;\n                        }\n                    }\n\n                    &.row li {\n                        &:hover {\n                            background-color: transparent;\n                        }\n                    }\n\n                    .notification-user {\n                        font-size: 15px;\n                        font-weight: 500;\n                        margin-bottom: 5px;\n                    }\n\n                    .notification-msg {\n                        margin-bottom: 5px;\n                    }\n\n                    .notification-time {\n                        font-size: 12px;\n                        color: #919aa3;\n                    }\n                }\n\n                .profile-notification {\n                    @extend .show-notification;\n                    width: 17em;\n\n                    a {\n                        font-size: 15px;\n                        padding-left: 0;\n                    }\n\n                    i {\n                        font-size: 15px;\n                        margin-left: 0;\n                        margin-right: 5px;\n                    }\n\n                    li {\n                        border-top: none;\n                        padding: 0.7em 20px;\n\n                        &:first-child {\n                            padding: 0.7em 20px;\n                        }\n                    }\n                }\n\n                &.active {\n                    &:before {\n                        content: \"\";\n                        border: 10px solid transparent;\n                        border-left-color: #fff;\n                        border-top-color: #fff;\n                        position: absolute;\n                        border-radius: 5px;\n                        bottom: -32px;\n                        right: 20px;\n                        box-shadow: -11px -11px 35px 0 rgba(0, 0, 0, 0.25);\n                        transform: rotate(45deg);\n                        z-index: 9;\n                    }\n                }\n            }\n\n            .badge {\n                border-radius: 100px;\n                right: 10px;\n                position: absolute;\n                top: 14px;\n                padding: 4px;\n\n                &:after,\n                &:before {\n                    content: \"\";\n                    width: 100%;\n                    height: 100%;\n                    border-radius: 50%;\n                    position: absolute;\n                    top: 0;\n                    left: 0;\n                    box-shadow: inset 0 0 20px 5px rgba(255, 255, 255, 0.4);\n                }\n\n                &:after {\n                    animation: not-blink 2.1s cubic-bezier(0.65, 0.815, 0.735, 0.395) infinite;\n                }\n\n                &:before {\n                    animation: not-blink 2.1s cubic-bezier(0.165, 0.84, 0.44, 1) infinite;\n                    animation-delay: 1.15s;\n                }\n            }\n        }\n\n        .header-search {\n            line-height: 0.8;\n\n            .main-search {\n                padding: 8px 0;\n                display: block;\n\n                .input-group {\n                    margin-bottom: 0;\n                    background-color: transparent;\n                    border-radius: 20px;\n\n                    .form-control {\n                        padding: 0.3rem 0.75rem;\n                        // display: none;\n                        width: 0;\n                        border: none;\n                        background-color: transparent;\n                    }\n\n                    .input-group-addon {\n                        padding: 0.3rem 0.75rem 0.3rem 0;\n                        background-color: transparent;\n                        border: none;\n\n                        &.search-close {\n                            padding: 0.3rem 0 0.3rem 0.75rem;\n                            display: none;\n                        }\n                    }\n                }\n\n                &.open {\n                    color: #000;\n\n                    .input-group {\n                        background-color: #fff;\n\n                        .form-control {\n                            display: block;\n                        }\n\n                        .input-group-addon {\n                            color: #000;\n\n                            &.search-close {\n                                display: block;\n                            }\n                        }\n                    }\n                }\n            }\n        }\n    }\n}\n@keyframes not-blink {\n    0% {\n        transform: scale(1);\n        opacity: 1;\n    }\n\n    100% {\n        transform: scale(4);\n        opacity: 0;\n    }\n}\n/*======= Main-header css end ======*/\n\n.pcoded {\n    .pcoded-container {\n        position: relative;\n        background: #f3f3f3;\n        box-shadow: 0 1px 20px 0 rgba(69, 90, 100, 0.08);\n    }\n\n    .pcoded-header {\n        position: relative;\n        display: block;\n    }\n\n    .pcoded-navbar {\n        display: block;\n        box-shadow: 1px 0px 20px 0px rgba(69,90,100,0.08);\n    }\n}\n\n.pcoded-main-container {\n    display: block;\n    position: relative;\n    background: #eff5f7;\n    min-height: calc(100vh - 56px);\n}\n\n.pcoded .pcoded-content {\n    position: relative;\n    display: block;\n}\n\n.pcoded-inner-navbar {\n    display: block;\n    position: relative;\n}\n\n.pcoded-inner-content {\n    padding: 10px;\n}\n\n.pcoded {\n    .pcoded-navbar .pcoded-item {\n        display: block;\n        list-style: none;\n        margin: 0;\n        padding: 0 0 20px 0;\n        position: relative;\n        &:after{\n            content: \"\";\n            background-color: #e4e9eb;\n            width: 80%;\n            height: 1px;\n            position: absolute;\n            left: 10%;\n            bottom: 10px;\n        }\n        > li {\n            display: block;\n            list-style: outside none none;\n            margin: 0;\n            padding: 0;\n            position: relative;\n\n            > a {\n                display: block;\n                font-size: 14px;\n                padding: 0 10px;\n                text-decoration: none;\n                position: relative;\n                transition: all 0.3s ease-in-out;\n\n                &.disabled {\n                    opacity: 0.5;\n                    cursor: not-allowed !important;\n                }\n            }\n\n            &.active {\n                > a {\n                    font-weight: 500;\n                }\n            }\n        }\n\n        .pcoded-hasm