focus {\n        @include buttons-focus(theme-color(success, hover));\n    }\n\n    &.disabled {\n        @include button-trans(theme-color(success, trans), theme-color(success, trans));\n    }\n}\n\n.btn-inverse {\n    @include buttons(theme-color(inverse, base), theme-color(inverse, base));\n\n    &:hover {\n        @include buttons-hover(theme-color(inverse, hover), theme-color(inverse, hover));\n    }\n\n    &:active {\n        @include buttons-active(theme-color(inverse, active), theme-color(inverse, active));\n    }\n\n    &:focus {\n        @include buttons-focus(theme-color(inverse, hover));\n    }\n\n    &.disabled {\n        @include button-trans(theme-color(inverse, trans), theme-color(inverse, trans));\n    }\n}\n\n.btn-info {\n    @include buttons(theme-color(info, base), theme-color(info, base));\n\n    &:hover {\n        @include buttons-hover(theme-color(info, hover), theme-color(info, hover));\n    }\n\n    &:active {\n        @include buttons-active(theme-color(info, active), theme-color(info, active));\n    }\n\n    &:focus {\n        @include buttons-focus(theme-color(info, hover));\n    }\n\n    &.disabled {\n        @include button-trans(theme-color(info, trans), theme-color(info, trans));\n    }\n}\n\n.btn-disabled {\n    @include buttons(theme-color(disabled, trans), theme-color(disabled, trans));\n}\n/**====== Basic-Buttons css end ======**/\n/**====== Outline-Buttons css Start ======**/\n.btn-outline-primary {\n    @include buttons-borders(theme-color(primary, active));\n    background-color: transparent;\n}\n\n.btn-outline-warning {\n    @include buttons-borders(theme-color(warning, active));\n    background-color: transparent;\n}\n.btn-outline-danger {\n    @include buttons-borders(theme-color(danger, active));\n    background-color: transparent;\n}\n\n.btn-outline-success {\n    @include buttons-borders(theme-color(success, active));\n    background-color: transparent;\n}\n\n.btn-outline-inverse {\n    @include buttons-borders(theme-color(inverse, active));\n    background-color: transparent;\n\n    &:hover {\n        color: #fff;\n    }\n}\n\n.btn-outline-warning {\n    @include buttons-borders(theme-color(warning, active));\n    background-color: transparent;\n}\n\n.btn-outline-info {\n    @include buttons-borders(theme-color(info, active));\n    background-color: transparent;\n}\n\n.btn-outline-disabled {\n    @include buttons-borders(theme-color(disabled, active));\n}\n/**====== Outline-Buttons css end ======**/\n/**====== gradient css Start ======**/\n.btn-grd-danger,\n.btn-grd-disabled,\n.btn-grd-info,\n.btn-grd-inverse,\n.btn-grd-primary,\n.btn-grd-success,\n.btn-grd-warning,\n.btn-grd-warning {\n    background-size: 200% auto;\n    transition: 0.5s ease-in-out;\n    color: #fff;\n\n    &:hover {\n        background-position: right center;\n    }\n\n    &.hor-grd {\n        background-size: auto 200%;\n\n        &:hover {\n            background-position: bottom center;\n        }\n    }\n}\n\n.btn-grd-primary {\n    background-image: linear-gradient(to right, theme-color(primary, hover) 0%, theme-color(primary, active) 51%, theme-color(primary, hover) 100%);\n\n    &.hor-grd {\n        background-image: linear-gradient(to top, theme-color(primary, hover) 0%, theme-color(primary, active) 51%, theme-color(primary, hover) 100%);\n    }\n}\n\n.btn-grd-warning {\n    background-image: linear-gradient(to right, theme-color(warning, hover) 0%, theme-color(warning, active) 51%, theme-color(warning, hover) 100%);\n\n    &.hor-grd {\n        background-image: linear-gradient(to top, theme-color(warning, hover) 0%, theme-color(warning, active) 51%, theme-color(warning, hover) 100%);\n    }\n}\n\n.btn-grd-danger {\n    background-image: linear-gradient(to right, theme-color(danger, hover) 0%, theme-color(danger, active) 51%, theme-color(danger, hover) 100%);\n\n    &.hor-grd {\n        background-image: linear-gradient(to top, theme-color(danger, hover) 0%, theme-color(danger, active) 51%, theme-color(danger, hover) 100%);\n    }\n}\n\n.btn-grd-success {\n    background-image: linear-gradient(to right, theme-color(success, hover) 0%, theme-color(success, active) 51%, theme-color(success, hover) 100%);\n\n    &.hor-grd {\n        background-image: linear-gradient(to top, theme-color(success, hover) 0%, theme-color(success, active) 51%, theme-color(success, hover) 100%);\n    }\n}\n\n.btn-grd-inverse {\n    background-image: linear-gradient(to right, theme-color(inverse, hover) 0%, theme-color(inverse, active) 51%, theme-color(inverse, hover) 100%);\n\n    &.hor-grd {\n        background-image: linear-gradient(to top, theme-color(inverse, hover) 0%, theme-color(inverse, active) 51%, theme-color(inverse, hover) 100%);\n    }\n}\n\n.btn-grd-info {\n    background-image: linear-gradient(to right, theme-color(info, hover) 0%, theme-color(info, active) 51%, theme-color(info, hover) 100%);\n\n    &.hor-grd {\n        background-image: linear-gradient(to top, theme-color(info, hover) 0%, theme-color(info, active) 51%, theme-color(info, hover) 100%);\n    }\n}\n\n.btn-grd-disabled {\n    background-image: linear-gradient(to right, theme-color(disabled, hover) 0%, theme-color(disabled, active) 51%, theme-color(disabled, hover) 100%);\n\n    &.hor-grd {\n        background-image: linear-gradient(to top, theme-color(disabled, hover) 0%, theme-color(disabled, active) 51%, theme-color(disabled, hover) 100%);\n    }\n}\n/**====== gradient-Buttons css end ======**/\n\n/**====== matrialized-Buttons css Start ======**/\n.btn.btn-mat{\n    position: relative;\n    border-radius: 0;\n    border: none;\n    &:before{\n        content: \"\";\n        position: absolute;\n        bottom: 0;\n        // width: 0;\n        left:50%;\n        right: 50%;\n        height: 3px;\n        transition: all 0.3s ease-in-out;\n    }\n    &:hover{\n        border: none;\n        &:before{\n            // width: 100%;\n            left:0;\n            right:0;\n        }\n    }\n}\n.btn-primary.btn-mat {\n    background-color: theme-color(primary, base);\n    &:before{\n        background-color: theme-color(primary, active);\n    }\n}\n\n.btn-warning.btn-mat {\n    background-color: theme-color(warning, base);\n    &:before{\n        background-color: theme-color(warning, active);\n    }\n}\n\n.btn-danger.btn-mat {\n    background-color: theme-color(danger, base);\n    &:before{\n        background-color: theme-color(danger, active);\n    }\n}\n\n.btn-success.btn-mat {\n    background-color: theme-color(success, base);\n    &:before{\n        background-color: theme-color(success, active);\n    }\n}\n\n.btn-inverse.btn-mat {\n    background-color: theme-color(inverse, base);\n    &:before{\n        background-color: theme-color(inverse, active);\n    }\n}\n\n.btn-info.btn-mat {\n    background-color: theme-color(info, base);\n    &:before{\n        background-color: theme-color(info, active);\n    }\n}\n\n.btn-outline-disabled {\n    background-color: theme-color(disabled, base);\n    &:before{\n        background-color: theme-color(disabled, active);\n    }\n}\n/**====== matrialized css end ======**/\n\n/**====== Button-Size css start ======**/\n.btn-xlg {\n    padding: 14px 20px;\n    font-size: 21px;\n    line-height: 40px;\n}\n\n.btn-lg {\n    padding: 12px 18px;\n    font-size: 18px;\n    line-height: 32px;\n}\n\n.btn-md {\n    padding: 10px 16px;\n    font-size: 15px;\n    line-height: 23px;\n}\n\n.btn-sm {\n    padding: 8px 14px;\n    line-height: 16px;\n    font-size: 11px;\n}\n\n.btn-mini {\n    padding: 5px 10px;\n    line-height: 14px;\n    font-size: 10px;\n}\n/**====== Button-Size css end ======**/\n/**====== Badges-button css start ======**/\n\n.btn-primary .badge {\n    @include labels-badge-inverse(theme-color(primary, base));\n}\n\n.btn-success .badge {\n    @include labels-badge-inverse(theme-color(success, base));\n}\n\n.btn-info .badge {\n    @include labels-badge-inverse(theme-color(info, base));\n}\n\n.btn-warning .badge {\n    @include labels-badge-inverse(theme-color(warning, base));\n}\n\n.btn-danger .badge {\n    @include labels-badge-inverse(theme-color(danger, base));\n}\n/**====== Badges-button css end ======**/\n/**====== Social-Buttons css start ======**/\n.btn-dribbble,\n.btn-dropbox,\n.btn-facebook,\n.btn-flickr,\n.btn-github,\n.btn-google-plus,\n.btn-instagram,\n.btn-linkedin,\n.btn-pinterest,\n.btn-skype,\n.btn-tumblr,\n.btn-twitter,\n.btn-youtube {\n    color: #fff;\n\n    &:focus,\n    &:hover {\n        color: #fff;\n        box-shadow: none;\n\n    }\n}\n\n.btn-facebook {\n    background-color: #3b5998;\n}\n\n.btn-twitter {\n    background-color: #00ACED;\n}\n\n.btn-linkedin {\n    background-color: #007BB6;\n}\n\n.btn-dribbble {\n    background-color: #EA4C89;\n}\n\n.btn-google-plus {\n    background-color: #DD4B39;\n}\n\n.btn-instagram {\n    background-color: #517FA4;\n}\n\n.btn-pinterest {\n    background-color: #CB2027;\n}\n\n.btn-dropbox {\n    background-color: #32506D;\n}\n\n.btn-tumblr {\n    background-color: #00ACED;\n}\n/**====== Social-Buttons css end ======**/\n/**====== Drop-down-Buttons css start ======**/\n\n.dropdown-primary,\n.dropdown-split-primary {\n    .dropdown-menu a {\n        &:hover {\n            @include dropdown-hover(theme-color(primary, base));\n        }\n    }\n}\n\n.dropdown-split-success,\n.dropdown-success {\n    .dropdown-menu a {\n        &:hover {\n            @include dropdown-hover(theme-color(success, base));\n        }\n    }\n}\n\n.dropdown-info,\n.dropdown-split-info {\n    .dropdown-menu a {\n        &:hover {\n            @include dropdown-hover(theme-color(info, base));\n        }\n    }\n}\n\n.dropdown-split-warning,\n.dropdown-warning {\n    .dropdown-menu a {\n        &:hover {\n            @include dropdown-hover(theme-color(warning, base));\n        }\n    }\n}\n\n.dropdown-danger,\n.dropdown-split-danger {\n    .dropdown-menu a {\n        &:hover {\n            @include dropdown-hover(theme-color(danger, base));\n        }\n    }\n}\n\n.dropdown-inverse,\n.dropdown-split-inverse {\n    .dropdown-item {\n        transition: all 0.3s ease-in;\n    }\n\n    .dropdown-item:hover {\n        @include dropdown-hover(theme-color(inverse, base));\n    }\n}\n/**====== Drop-down-Buttons css end ======**/\n/**====== Theme-Buttons css end ======**/\n","// Button mixins start\n@mixin buttons($back-color,$border-color,$color:#fff,$cursor:pointer,$transition:all ease-in 0.3s){\n\tbackground-color: $back-color;\n\tborder-color:$border-color;\n\tcolor:$color;\n\tcursor:$cursor;\n\ttransition: $transition;\n}\n\n@mixin buttons-hover($back-color,$border-color,$color:#fff){\n\tbackground-color: $back-color;\n\tborder-color:$border-color;\n}\n\n@mixin animations-frame($name){\n\t@-webkit-keyframes #{$name}{\n\t\t@content;\n\t}\n\t@keyframes #{$name}{\n\t\t@content;\n\t}\n}\n\n@mixin buttons-active($back-color,$border-color,$color:#fff,$box-shadow:none){\n\tbackground-color: $back-color !important;\n\tborder-color:$border-color;\n\tbox-shadow:$box-shadow;\n\tcolor:$color;\n}\n\n@mixin labels-active($back-color,$border-color,$color:#fff,$box-shadow:none){\n\tbackground-color: $back-color !important;\n\tborder-color:$border-color;\n\tbox-shadow:$box-shadow;\n\tcolor:$color;\n}\n\n@mixin button-trans($back-color,$border-color){\n\tbackground-color:$back-color;\n\tborder-color:$border-color;\n}\n\n@mixin buttons-focus($back-color,$color:#fff,$box-shadow:none){\n\tbox-shadow:$box-shadow;\n\tcolor:$color;\n\tbackground-color:$back-color;\n}\n\n@mixin buttons-borders($color,$back-color:#fff){\n\tcolor:$color;\n\tbackground-color:$back-color;\n}\n\n@mixin dropdown-hover($back-color,$color:#fff){\n\tbackground-color:$back-color;\n\tcolor:$color;\n}\n// Button mixins end\n\n// Label mixin start\n@mixin labels-inverse($border-color,$color){\n\tborder-color: $border-color;\n\tcolor:$color !important;\n}\n\n@mixin labels-badge($back-color:#34495e,$color:#fff){\n\tbackground-color: $back-color;\n\tcolor:$color;\n}\n@mixin labels-badge-inverse($color,$back-color:#fff){\n\tcolor:$color;\n\tbackground-color: $back-color;\n}\n// Label mixin end\n\n\n@mixin width($width){\n\twidth:$width;\n}\n\n\n@mixin shadow($shadow...){\n\tbox-shadow:$shadow;\n\n}\n\n\n@mixin theme-keyframes($name) {\n\t@-webkit-keyframes #{$name} {\n        @content;\n    }\n\t@keyframes #{$name} {\n        @content;\n    }\n}\n","/**  =====================\n      Basic form elements css start\n==========================  **/\n.col-form-label {\n    font-size: 14px;\n}\n\n.select2-container {\n    width: 100% !important;\n}\n\n.form-group {\n    margin-bottom: 1.25em;\n\n    .form-control-lg {\n        font-size: 1.25em;\n    }\n\n    .form-control-round {\n        border-radius: 50px;\n    }\n\n    .form-control-static {\n        &:focus {\n            outline: none;\n        }\n    }\n}\n\n.form-control {\n    font-size: 13px;\n    border-radius: 2px;\n    border: 1px solid rgb(204, 204, 204);\n    height: 18px;\n\n    &:focus {\n        border: 1px solid $primary-color;\n    }\n\n    &.form-control-normal {\n        font-weight: 400;\n    }\n\n    &.form-control-bold {\n        font-weight: 600;\n    }\n\n    &.form-control-capitalize {\n        text-transform: capitalize;\n    }\n\n    &.form-control-uppercase {\n        text-transform: uppercase;\n    }\n\n    &.form-control-lowercase {\n        text-transform: lowercase;\n    }\n\n    &.form-control-variant {\n        font-variant: small-caps;\n    }\n\n    &.form-control-left {\n        text-align: left;\n    }\n\n    &.form-control-center {\n        text-align: center;\n    }\n\n    &.form-control-right {\n        text-align: right;\n    }\n\n    &.form-control-rtl {\n        direction: rtl;\n    }\n\n    &:disabled {\n        cursor: not-allowed;\n        opacity: 0.5;\n    }\n}\n/*===== Border Inputs start =====*/\n$form-border-state: primary, warning, default, danger, success, inverse, info;\n$form-border-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\n@each $var in $form-border-state {\n    $i: index($form-border-state, $var);\n\n    .form-control-#{$var} {\n        border-color: nth($form-border-color, $i);\n        color: nth($form-border-color, $i);\n\n        &:focus {\n            border-color: nth($form-border-color, $i);\n        }\n    }\n\n    .input-group-#{$var} {\n        .input-group-addon {\n            background-color: nth($form-border-color, $i);\n        }\n\n        .form-control {\n            border-color: nth($form-border-color, $i);\n        }\n    }\n}\n/*===== Form Input-Text start =====*/\n$form-txt-state: primary, warning, default, danger, success, inverse, info;\n$form-txt-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\n@each $var in $form-txt-state {\n    $i: index($form-txt-state, $var);\n\n    .form-txt-#{$var} {\n        color: nth($form-txt-color, $i);\n\n        &:focus {\n            color: nth($form-txt-color, $i);\n        }\n\n        &::-moz-placeholder {\n            color: nth($form-txt-color, $i);\n            opacity: 1;\n        }\n\n        &:-ms-input-placeholder {\n            color: nth($form-txt-color, $i);\n        }\n\n        &::-webkit-input-placeholder {\n            color: nth($form-txt-color, $i);\n        }\n    }\n}\n/*===== Form Background-inputs start =====*/\n$form-bg-state: primary, warning, default, danger, success, inverse, info;\n$form-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\n@each $var in $form-bg-state {\n    $i: index($form-bg-state, $var);\n\n    .form-bg-#{$var} {\n        background-color: nth($form-bg-color, $i);\n        border-color: nth($form-bg-color, $i);\n        color: #fff;\n\n        &:focus {\n            background-color: nth($form-bg-color, $i);\n            border-color: nth($form-bg-color, $i);\n            color: #fff;\n        }\n\n        &::-moz-placeholder {\n            color: #fff;\n            opacity: 1;\n        }\n\n        &:-ms-input-placeholder {\n            color: #fff;\n        }\n\n        &::-webkit-input-placeholder {\n            color: #fff;\n        }\n    }\n}\n/*===== Form Validation states start =====*/\n$form-valid-state: success, warning, danger;\n$form-valid-color: $success-color, $warning-color, $danger-color;\n@each $var in $form-valid-state {\n    $i: index($form-valid-state, $var);\n\n    .has-#{$var} {\n        .col-form-label {\n            color: nth($form-valid-color, $i);\n        }\n\n        .form-control-success {\n            border-color: nth($form-valid-color, $i);\n        }\n    }\n}\n/*===== Form-icon css starts =====*/\n.left-icon-control {\n    position: relative;\n\n    input {\n        padding-left: 50px;\n    }\n\n    .form-icon {\n        position: absolute;\n        top: 50%;\n        left: 17px;\n        margin-top: -12px;\n    }\n}\n\n.right-icon-control {\n    position: relative;\n\n    input {\n        padding-right: 50px;\n    }\n\n    .form-icon {\n        position: absolute;\n        bottom: 8px;\n        right: 8px;\n\n        .btn.btn-icon {\n            width: 35px;\n            height: 35px;\n            padding: 0;\n        }\n    }\n    .btn > i{\n        margin-right: 0;\n    }\n\n}\n/*===== Form-icon css ends =====*/\n/*===== Basic form elements ends =====*/\n/**  =====================\n      Form group-add-on css start\n==========================  **/\n.input-group {\n    margin-bottom: 1.25em;\n}\n\n.input-group-addon {\n    background-color: $primary-color;\n    color: $white-txt;\n}\n// .input-group-button [class*=btn-] {\n//     padding: 0;\n//     margin-bottom: 0;\n// }\n\n.input-group-button .input-group-addon {\n    transition: all ease-in 0.3s;\n\n    &:hover {\n        @include buttons-hover(theme-color(primary, hover), theme-color(primary, hover));\n\n        .btn {\n            @include buttons-hover(theme-color(primary, hover), theme-color(primary, hover));\n        }\n    }\n}\n\n.input-group-dropdown [class*=btn-] {\n    margin-bottom: 0;\n}\n/*===== Form group-add-on ends =====*/\n/**  =====================\n      Switches css start\n==========================  **/\n.js-dynamic-disable,\n.js-dynamic-enable {\n    vertical-align: inherit;\n}\n/*===== Switches css ends =====*/\n/**  =====================\n      Radio-button css start\n==========================  **/\n.form-radio {\n    position: relative;\n\n    .form-help {\n        position: absolute;\n        width: 100%;\n    }\n\n    label {\n        position: relative;\n        padding-left: 1.5rem;\n        text-align: left;\n        color: #333;\n        display: block;\n        line-height: 1.8;\n    }\n\n    input {\n        width: auto;\n        opacity: 0.00000001;\n        position: absolute;\n        left: 0;\n    }\n}\n\n.radio {\n    .helper {\n        position: absolute;\n        top: -0.15rem;\n        left: -0.25rem;\n        cursor: pointer;\n        display: block;\n        font-size: 1rem;\n        -webkit-user-select: none;\n        -moz-user-select: none;\n        -ms-user-select: none;\n        user-select: none;\n        color: #999;\n\n        &::after {\n            transform: scale(0);\n            background-color: $primary-color;\n            border-color: black;\n        }\n\n        &::after,\n        &::before {\n            content: '';\n            position: absolute;\n            left: 0;\n            top: 3px;\n            margin: 0.25rem;\n            width: 1rem;\n            height: 1rem;\n            -webkit-transition: -webkit-transform 0.28s ease;\n            transition: -webkit-transform 0.28s ease;\n            transition: transform 0.28s ease, -webkit-transform 0.28s ease;\n            border-radius: 50%;\n            border: 0.125rem solid black;\n        }\n    }\n\n    label:hover .helper {\n        color: $primary-color;\n    }\n\n    input {\n        &:checked ~ .helper::after {\n            transform: scale(0.5);\n        }\n\n        &:checked ~ .helper::before {\n            color: $primary-color;\n            animation: ripple 0.3s linear forwards;\n        }\n    }\n\n    &.radiofill {\n        input:checked ~ .helper::after {\n            transform: scale(1);\n        }\n\n        .helper::after {\n            background-color: $primary-color;\n        }\n    }\n\n    &.radio-outline {\n        input:checked ~ .helper::after {\n            transform: scale(0.6);\n        }\n\n        .helper::after {\n            background-color: #fff;\n            border: 0.225rem solid $primary-color;\n        }\n    }\n\n    &.radio-matrial {\n        input ~ .helper::after {\n            background-color: #fff;\n        }\n\n        input:checked ~ .helper::after {\n            transform: scale(0.5);\n            box-shadow: 0 1px 7px -1px rgba(0, 0, 0, 0.72);\n        }\n\n        input:checked ~ .helper::before {\n            background-color: $primary-color;\n        }\n    }\n\n    &.radio-disable {\n        label {\n            cursor: not-allowed;\n        }\n        opacity: 0.7;\n    }\n}\n\n.radio-inline {\n    display: inline-block;\n    margin-right: 20px;\n}\n$radio-bg-state: primary, warning, default, danger, success, inverse, info;\n$radio-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\n@each $var in $radio-bg-state {\n    $i: index($radio-bg-state, $var);\n\n    .radio {\n        &.radiofill {\n            &.radio-#{$var} {\n                .helper::after {\n                    background-color: nth($radio-bg-color, $i);\n                    border-color: nth($radio-bg-color, $i);\n                }\n\n                .helper::before {\n                    border-color: nth($radio-bg-color, $i);\n                }\n            }\n        }\n\n        &.radio-outline {\n            &.radio-#{$var} {\n                .helper::after {\n                    background-color: #fff;\n                    border: 0.225rem solid nth($radio-bg-color, $i);\n                }\n\n                .helper::before {\n                    border-color: nth($radio-bg-color, $i);\n                }\n            }\n        }\n\n        &.radio-matrial {\n            &.radio-#{$var} {\n                input ~ .helper::after {\n                    background-color: #fff;\n                    border-color: #fff;\n                }\n\n                input ~ .helper::before {\n                    background-color: nth($radio-bg-color, $i);\n                    border-color: nth($radio-bg-color, $i);\n                }\n            }\n        }\n    }\n}\n@keyframes ripple {\n    0% {\n        box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.0);\n    }\n    50% {\n        box-shadow: 0px 0px 0px 10px rgba(0, 0, 0, 0.05);\n    }\n    100% {\n        box-shadow: 0px 0px 0px 10px rgba(0, 0, 0, 0);\n    }\n}\n/*===== Radio Button css ends =====*/\n/*===== Border-checkbox css start =====*/\n.border-checkbox-section {\n    .border-checkbox-group {\n        display: inline-block;\n        .border-checkbox:checked + .border-checkbox-label:before {\n            animation: ripple 0.3s linear forwards;\n        }\n        .border-checkbox:checked + .border-checkbox-label:after {\n            -webkit-animation: check linear 0.5s;\n            animation: check linear 0.5s;\n            opacity: 1;\n            border-color:rgb(241, 13, 13);\n\n            .border-checkbox-label {\n                &:before {\n                    border-color: #eee;\n                }\n            }\n        }\n        .border-checkbox-label {\n            position: relative;\n            display: inline-block;\n            cursor: pointer;\n            height: 3em;\n            line-height: 20px;\n            padding-left: 30px;\n            margin-right: 15px;\n\n            &:after {\n                content: \"\";\n                display: block;\n                width: 6px;\n                height: 12px;\n                opacity: 0.9;\n                border-right: 2px solid #eee;\n                border-top: 2px solid #eee;\n                position: absolute;\n                left: 4px;\n                top: 11px;\n                -webkit-transform: scaleX(-1) rotate(135deg);\n                transform: scaleX(-1) rotate(135deg);\n                -webkit-transform-origin: left top;\n                transform-origin: left top;\n            }\n\n            &:before {\n                content: \"\";\n                display: block;\n                border: 2px solid $primary-color;\n                width: 20px;\n                height: 20px;\n                position: absolute;\n                left: 0;\n            }\n        }\n    }\n\n    // .border-checkbox:focus{\n    //     border-color: 5px solid gold;\n    // }\n\n    .border-checkbox {\n        display: none;\n\n        &:disabled {\n            ~ .border-checkbox-label {\n                cursor: no-drop;\n                color: #ccc;\n            }\n        }\n    }\n}\n@include theme-keyframes(check) {\n    0% {\n        height: 0;\n        width: 0;\n    }\n\n    25% {\n        height: 0;\n        width: 6px;\n    }\n\n    50% {\n        height: 12px;\n        width: 6px;\n    }\n}\n$border-checkbox-bg-state: primary, warning, default, danger, success, inverse, info;\n$border-checkbox-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\n@each $var in $border-checkbox-bg-state {\n    $i: index($border-checkbox-bg-state, $var);\n\n    .border-checkbox-section .border-checkbox-group-#{$var} {\n        .border-checkbox-label:before {\n            // border: 2px solid nth($border-checkbox-bg-color, $i);\n            border: 2px solid black;\n        }\n\n        .border-checkbox:checked + .border-checkbox-label:after {\n            // border-color: nth($border-checkbox-bg-color, $i);\n            border-color: black;\n        }\n    }\n}\n/*===== zoom Fade-in-checkbox css start =====*/\n.checkbox-fade,\n.checkbox-zoom {\n    display: inline-block;\n    margin-right: 15px;\n\n    label {\n        line-height: 20px;\n\n        input[type=\"checkbox\"],\n        input[type=\"radio\"] {\n            display: none;\n\n            &:checked + .cr > .cr-icon {\n                transform: scale(1) rotateZ(0deg);\n                opacity: 1;\n            }\n\n            + .cr > .cr-icon {\n                transform: scale(3) rotateZ(-20deg);\n                opacity: 0;\n                transition: all 0.3s ease-in;\n            }\n        }\n\n        &:after {\n            content: '';\n            display: table;\n            clear: both;\n        }\n    }\n\n    &.fade-in-disable {\n        .cr,\n        label {\n            color: #ccc;\n            cursor: no-drop;\n        }\n    }\n\n    .cr {\n        border-radius: 0;\n        border: 2px solid $primary-color;\n        cursor: pointer;\n        display: inline-block;\n        float: left;\n        height: 20px;\n        margin-right: 1rem;\n        position: relative;\n        width: 20px;\n\n        .cr-icon {\n            color: $primary-color;\n            font-size: 0.8em;\n            left: 0;\n            line-height: 0;\n            position: absolute;\n            right: 0;\n            text-align: center;\n            top: 50%;\n        }\n    }\n    input[type=\"checkbox\"]:checked{\n         + .cr{\n             animation: ripple 0.3s linear forwards;\n         }\n    }\n}\n\n.checkbox-fade {\n    label {\n        line-height: 20px;\n\n        input[type=\"checkbox\"],\n        input[type=\"radio\"] {\n            display: none;\n\n            &:checked + .cr > .cr-icon {\n                transform: scale(1) rotateZ(0deg);\n                opacity: 1;\n            }\n\n            + .cr > .cr-icon {\n                transform: scale(3) rotateZ(-20deg);\n                opacity: 0;\n                transition: all 0.3s ease-in;\n            }\n        }\n    }\n}\n\n.checkbox-zoom {\n    label {\n        line-height: 20px;\n\n        input[type=\"checkbox\"],\n        input[type=\"radio\"] {\n            display: none;\n\n            &:checked + .cr > .cr-icon {\n                transform: scale3d(1, 1, 1) translate3d(0, 0, 0);\n                opacity: 1;\n            }\n\n            + .cr > .cr-icon {\n                transform: scale3d(0.2, 0.2, .1) translate3d(0, 0, 0);\n                opacity: 0;\n                transition: all 0.3s ease-in;\n            }\n        }\n    }\n}\n$fade-in-checkbox-bg-state: primary, warning, default, danger, success, inverse, info;\n$fade-in-checkbox-bg-color: $primary-color, $warning-color, $default-color, $danger-color, $success-color, $inverse-color, $info-color;\n@each $var in $fade-in-checkbox-bg-state {\n    $i: index($fade-in-checkbox-bg-state, $var);\n\n    .checkbox-fade,\n    .checkbox-zoom {\n        &.fade-in-#{$var},\n        &.zoom-#{$var} {\n            .cr {\n                border: 2px solid nth($fade-in-checkbox-bg-color, $i);\n\n                .cr-icon {\n                    color: nth($fade-in-checkbox-bg-color, $i);\n                }\n            }\n        }\n    }\n}\n/*===== Color-checkbox css start =====*/\n.checkbox-color {\n    display: inline-block;\n    margin-right: 20px;\n    cursor: pointer;\n\n    label {\n        display: inline-block;\n        position: relative;\n        padding-left: 10px;\n        line-height: 20px;\n\n        &::before {\n            content: \"\";\n            display: inline-block;\n            position: absolute;\n            top: 0;\n            width: 20px;\n            height: 20px;\n            left: 0;\n            right: 0;\n            text-align: center;\n            margin-left: -20px;\n            border: 1px solid #ccc;\n            border-radius: 0;\n            background-color: #fff;\n            transition: border 0.15s ease-in-out, color 0.15s ea