 width: 100px;\n    height: 100px;\n    position: relative;\n    transform: translate(-50%, -50%);\n    &.animation-start .circle {\n        animation: rotating 2s ease-in-out infinite;\n    }\n    &.animation-stop .circle {\n        animation-iteration-count: 1;\n    }\n    .circle {\n        width: 50px;\n        height: 50px;\n        display: block;\n        position: absolute;\n        transform-origin: 50% 50%;\n        animation-iteration-count: 0;\n    }\n    .circle:after {\n        content: '';\n        top: 0;\n        left: 0;\n        width: 10px;\n        height: 10px;\n        display: block;\n        border-radius: 50%;\n        transform: translate(-50%, -50%);\n        background-color: #1abc9c;\n    }\n    .circle.delay-1 {\n        animation-delay: 0.125s;\n    }\n    .circle.size-1:after {\n        width: 11px;\n        height: 11px;\n    }\n    .circle.delay-2 {\n        animation-delay: 0.25s;\n    }\n    .circle.size-2:after {\n        width: 12px;\n        height: 12px;\n    }\n    .circle.delay-3 {\n        animation-delay: 0.375s;\n    }\n    .circle.size-3:after {\n        width: 13px;\n        height: 13px;\n    }\n    .circle.delay-4 {\n        animation-delay: 0.5s;\n    }\n    .circle.size-4:after {\n        width: 14px;\n        height: 14px;\n    }\n    .circle.delay-5 {\n        animation-delay: 0.625s;\n    }\n    .circle.size-5:after {\n        width: 15px;\n        height: 15px;\n    }\n    .circle.delay-6 {\n        animation-delay: 0.75s;\n    }\n    .circle.size-6:after {\n        width: 16px;\n        height: 16px;\n    }\n    .circle.delay-7 {\n        animation-delay: 0.875s;\n    }\n    .circle.size-7:after {\n        width: 17px;\n        height: 17px;\n    }\n    .circle.delay-8 {\n        animation-delay: 1s;\n    }\n    .circle.size-8:after {\n        width: 18px;\n        height: 18px;\n    }\n    .circle.delay-9 {\n        animation-delay: 1.125s;\n    }\n    .circle.size-9:after {\n        width: 19px;\n        height: 19px;\n    }\n    .circle.delay-10 {\n        animation-delay: 1.25s;\n    }\n    .circle.size-10:after {\n        width: 20px;\n        height: 20px;\n    }\n    .circle.delay-11 {\n        animation-delay: 1.375s;\n    }\n    .circle.size-11:after {\n        width: 21px;\n        height: 21px;\n    }\n    .circle.delay-12 {\n        animation-delay: 1.5s;\n    }\n    .circle.size-12:after {\n        width: 22px;\n        height: 22px;\n    }\n}\n\n@keyframes rotating {\n    0% {\n        transform: rotate(0deg);\n    }\n    100% {\n        transform: rotate(360deg);\n    }\n}\n\n\n/*===== Loader2 =====*/\n\n#loader2 {\n    width: 80px;\n    height: 130px;\n    display: block;\n    transform-origin: 50% 50%;\n    top: calc(50% - 50px);\n    left: calc(50% - 50px);\n    animation: spin 1s linear infinite;\n    margin: 0 auto;\n}\n\n#circle-loader2 {\n    fill: none;\n    stroke: $primary-color;\n    stroke-width: 4;\n    animation: draw 3s ease-in-out infinite;\n}\n\n@keyframes draw {\n    0% {\n        stroke-dasharray: 20, 282.6;\n    }\n    50% {\n        stroke-dasharray: 200, 282.6;\n    }\n    100% {\n        stroke-dasharray: 20, 282.6;\n    }\n}\n\n@keyframes spin {\n    to {\n        transform: rotate(360deg);\n    }\n}\n\n\n/*==== Loader3 ====*/\n\n.preloader3 {\n    width: auto;\n    height: 130px;\n    text-align: center;\n    font-size: 10px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    > div {\n        margin: 2px;\n        background-color: $primary-color;\n        height: 10px;\n        width: 10px;\n        border-radius: 50%;\n        display: inline-block;\n        -webkit-animation: stretchdelay 0.7s infinite ease-in-out;\n        animation: stretchdelay 0.7s infinite ease-in-out;\n    }\n    .circ2 {\n        -webkit-animation-delay: -0.6s;\n        animation-delay: -0.6s;\n    }\n    .circ3 {\n        -webkit-animation-delay: -0.5s;\n        animation-delay: -0.5s;\n    }\n    .circ4 {\n        -webkit-animation-delay: -0.4s;\n        animation-delay: -0.4s;\n    }\n    .circ5 {\n        -webkit-animation-delay: -0.3s;\n        animation-delay: -0.3s;\n    }\n    .loader-md {\n        height: 13px;\n        width: 13px;\n    }\n    .loader-lg {\n        height: 18px;\n        width: 18px;\n    }\n}\n\n@-webkit-keyframes stretchdelay {\n    0%,\n    40%,\n    100% {\n        -webkit-transform: translateY(-10px)\n    }\n    20% {\n        -webkit-transform: translateY(-20px)\n    }\n}\n\n@keyframes stretchdelay {\n    0%,\n    40%,\n    100% {\n        transform: translateY(-10px);\n        -webkit-transform: translateY(-10px);\n    }\n    20% {\n        transform: translateY(-20px);\n        -webkit-transform: translateY(-20px);\n    }\n}\n\n@keyframes rotateColor {\n    0% {\n        border-top-color: #1b8bf9;\n    }\n    12.5% {\n        border-bottom-color: rgba(27, 139, 249, 0.8);\n    }\n    25% {\n        border-bottom-color: rgba(27, 139, 249, 0.6);\n    }\n    37.5% {\n        border-bottom-color: rgba(27, 139, 249, 0.4);\n    }\n    50% {\n        border-bottom-color: rgba(27, 139, 249, 0.6);\n    }\n    50.00001% {\n        border-bottom-color: #1b8bf9;\n    }\n    62.5% {\n        border-bottom-color: rgba(27, 139, 249, 0.8);\n    }\n    75% {\n        border-bottom-color: rgba(27, 139, 249, 0.6);\n    }\n    87.5% {\n        border-bottom-color: rgba(27, 139, 249, 0.4);\n    }\n}\n\n\n/*===== Loader4 =====*/\n\n.preloader4 {\n    width: 30px;\n    height: 30px;\n    position: relative;\n    margin: 55px auto;\n}\n\n.double-bounce1,\n.double-bounce2 {\n    width: 100%;\n    height: 100%;\n    border-radius: 50%;\n    background-color: $primary-color;\n    opacity: 0.6;\n    position: absolute;\n    top: 0;\n    left: 0;\n    -webkit-animation: bounce 2.0s infinite ease-in-out;\n    animation: bounce 2.0s infinite ease-in-out;\n    -webkit-animation-delay: -1.0s;\n    animation-delay: -1.0s;\n}\n\n@-webkit-keyframes bounce {\n    0%,\n    100% {\n        -webkit-transform: scale(0.0)\n    }\n    50% {\n        -webkit-transform: scale(1.0)\n    }\n}\n\n@keyframes bounce {\n    0%,\n    100% {\n        transform: scale(0.0);\n        -webkit-transform: scale(0.0);\n    }\n    50% {\n        transform: scale(1.0);\n        -webkit-transform: scale(1.0);\n    }\n}\n\n\n/*===== Loader5 =====*/\n\n.preloader5 {\n    margin: 55px 0;\n    text-align: center;\n    .circle-5 {\n        display: inline-block;\n        padding: 11px;\n        margin: 0 0.6em;\n        background: $primary-color;\n        border-radius: 100%;\n    }\n    .l {\n        -webkit-animation: pulse 2s infinite linear;\n        animation: pulse 2s infinite linear;\n    }\n    .m {\n        -webkit-animation: pulse 2s infinite linear;\n        animation: pulse 2s infinite linear;\n        -webkit-animation-delay: 0.3s;\n        animation-delay: 0.3s;\n    }\n    .r {\n        -webkit-animation: pulse 2s infinite linear;\n        animation: pulse 2s infinite linear;\n        -webkit-animation-delay: 0.6s;\n        animation-delay: 0.6s;\n    }\n}\n\n@-webkit-keyframes pulse {\n    10% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    20% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    30% {\n        -webkit-transform: scale(1.7);\n        transform: scale(1.7);\n    }\n    50% {\n        -webkit-transform: scale(1.7);\n        transform: scale(1.7);\n    }\n    70% {\n        -webkit-transform: scale(1.7);\n        transform: scale(1.7);\n    }\n    80% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    90% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    100% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n}\n\n@keyframes pulse {\n    10% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    20% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    30% {\n        -webkit-transform: scale(1.7);\n        transform: scale(1.7);\n    }\n    50% {\n        -webkit-transform: scale(1.7);\n        transform: scale(1.7);\n    }\n    70% {\n        -webkit-transform: scale(1.7);\n        transform: scale(1.7);\n    }\n    80% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    90% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n    100% {\n        -webkit-transform: scale(1);\n        transform: scale(1);\n    }\n}\n\n\n/*===== Loader6 =====*/\n\n.preloader6 {\n    border-radius: 100%;\n    width: 100px;\n    height: 100px;\n    position: relative;\n    overflow: hidden;\n    z-index: 1;\n    background: inherit;\n    margin: 18px 0;\n}\n\n.preloader6 hr {\n    border: 0;\n    background: inherit;\n    width: 80%;\n    height: 80%;\n    margin: 10%;\n    border-radius: 100%;\n}\n\n.preloader6::before,\n.preloader6::after,\n.preloader6 hr::before,\n.preloader6 hr::after {\n    content: '';\n    position: absolute;\n    top: 0;\n    z-index: -1;\n    left: 33px;\n    border: 17px solid transparent;\n    border-bottom: 50px solid $primary-color;\n    border-top: 50px solid $primary-color;\n    animation: rotateColor 1000ms infinite;\n}\n\n.preloader6::after {\n    transform: rotate(45deg);\n    animation-delay: 125ms;\n}\n\n.preloader6 hr::before {\n    transform: rotate(90deg);\n    animation-delay: 250ms;\n}\n\n.preloader6 hr::after {\n    transform: rotate(135deg);\n    animation-delay: 375ms;\n}\n\n.loader-block,\n.loader {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@keyframes rotateColor {\n    0% {\n        border-top-color: rgba(26, 188, 156, 1)\n    }\n    12.5% {\n        border-bottom-color: rgba(26, 188, 156, 0.8)\n    }\n    25% {\n        border-bottom-color: rgba(26, 188, 156, 0.6)\n    }\n    37.5% {\n        border-bottom-color: rgba(26, 188, 156, 0.4)\n    }\n    50% {\n        border-bottom-color: rgba(26, 188, 156, 0.6)\n    }\n    50.00001% {\n        border-bottom-color: rgba(26, 188, 156, 1);\n    }\n    62.5% {\n        border-bottom-color: rgba(26, 188, 156, 0.8);\n    }\n    75% {\n        border-bottom-color: rgba(26, 188, 156, 0.6);\n    }\n    87.5% {\n        border-bottom-color: rgba(26, 188, 156, 0.4);\n    }\n}\n\n\n/*========= Pre-loader css end ============*/","/*range slider start */\n\n.range-slider {\n    min-height: 200px;\n    display: flex;\n    align-items: center;\n    margin-left: 15px;\n    b{\n        margin-top: -10px;\n    }\n}\n\n#RGB {\n    height: 10px;\n    background: rgb(128, 128, 128);\n}\n\n#RC .slider-selection {\n    background: #FF8282;\n}\n\n#RC .slider-handle {\n    background: red;\n}\n\n#GC .slider-selection {\n    background: #428041;\n}\n\n#GC .slider-handle {\n    background: green;\n}\n\n#BC .slider-selection {\n    background: #8283FF;\n}\n\n#BC .slider-handle {\n    border-bottom-color: blue;\n}\n\n#R,\n#G,\n#B {\n    width: 300px;\n}\n\n.range-slider-contain {\n    position: absolute;\n    bottom: 15px;\n}\n\n.slider-handle {\n    top: -5px;\n    background-image: linear-gradient(to bottom, $primary-color 0%, $primary-color 100%);\n    box-shadow: none;\n}\n\n.slider-tick,\n.slider-track {\n    background-image: linear-gradient(to bottom, #BDC3C7 0%, #BDC3C7 100%)\n}\n\n.slider-handle.custom::before {\n    line-height: 15px;\n    font-size: 35px;\n    color: $primary-color;\n}\n\n#slider12a,\n#slider12c {\n    .slider-track-high {\n        background: green;\n    }\n}\n\n#slider12b,\n#slider12c {\n    .slider-track-low {\n        background: red;\n    }\n}\n\n#slider12c .slider-selection {\n    background: yellow;\n}\n\n.slider-selection.tick-slider-selection {\n    background-image: linear-gradient(to bottom, lighten($primary-color,10%) 0%, lighten($primary-color,10%) 100%);\n}\n\n.slider-tick.in-selection {\n    background-image: linear-gradient(to bottom, lighten($primary-color,10%) 0%, lighten($primary-color,10%) 100%);\n}\n\n.slider.slider-horizontal {\n    .slider-track {\n        height: 2px;\n    }\n    .slider-tick,\n    .slider-handle {\n        top: -5px;\n        cursor: pointer;\n    }\n}\n\n\n/*range slider end */\n\n\n.slider.slider-horizontal .slider-track {\n    width: 90%;\n    margin-top: -5px;\n    top: 50%;\n    left: 10px;\n}\n","/**  =====================\n      Rating css start\n==========================  **/\n\n.br-wrapper {\n    margin: 20px 0 50px;\n}\n\n.br-theme-bars-1to10 .br-widget .br-current-rating {\n    clear: both;\n    padding: 0;\n    font-size: 13px;\n    color: #757575;\n    font-weight: 400;\n}\n\n.br-theme-bars-reversed .br-widget .br-current-rating {\n    clear: both;\n    padding: 0;\n    line-height: 2;\n    font-size: 13px;\n}\n\n.br-theme-bars-movie .br-widget .br-current-rating {\n    color: #757575;\n    font-weight: 400;\n    text-align: left;\n}\n\n.br-theme-bars-horizontal,\n.br-theme-bars-reversed {\n    .br-widget .br-current-rating {\n        color: #757575;\n        font-weight: 400;\n        font-size: 13px;\n    }\n}\n\nspan.value {\n    display: inline;\n}\n\n.current-rating {\n    position: relative;\n    top: -50px;\n    &.hidden {\n        display: none;\n    }\n}\n\n.your-rating {\n    position: relative;\n    top: -50px;\n    &.hidden {\n        display: none;\n    }\n}\n\n.br-theme-bars-1to10,\n.br-theme-bars-movie,\n.br-theme-bars-reversed,\n.br-theme-bars-horizontal {\n    .br-widget a {\n        background-color: lighten($primary-color, 8%);\n        &.br-active,\n        &.br-selected {\n            background-color: $primary-color;\n        }\n    }\n}\n\n.br-theme-bars-pill {\n    .br-widget a {\n        background-color: lighten($primary-color, 8%);\n        color: $primary-color;\n        &.br-active,\n        &.br-selected {\n            background-color: $primary-color;\n        }\n    }\n}\n\n.br-theme-fontawesome-stars {\n    .br-widget {\n        a.br-active:after,\n        a.br-selected:after {\n            color: $primary-color;\n        }\n    }\n}\n\n.br-theme-css-stars {\n    .br-widget {\n        a.br-active:after,\n        a.br-selected:after {\n            color: $primary-color;\n        }\n    }\n}\n\n.br-theme-bars-square {\n    .br-widget a {\n        border: 2px solid lighten($primary-color, 8%);\n        color: lighten($primary-color, 8%);\n        &.br-active,\n        &.br-selected {\n            border: 2px solid $primary-color;\n            color: $primary-color;\n        }\n    }\n}\n\n.br-theme-fontawesome-stars-o {\n    .br-widget {\n        a.br-selected:after,\n        a:after,\n        a.br-active:after,\n        a.br-fractional:after {\n            color: $primary-color;\n        }\n    }\n}\n\n/**====== Rating css end ======**/\n","/**  =====================\n      Todo css start\n==========================  **/\n\n#task-container {\n    ul {\n        overflow: hidden;\n    }\n    .task-headline {\n        display: none;\n        color: #666666;\n        border-bottom: 1px solid #C8C7BB;\n        padding-bottom: 20px;\n        margin-bottom: 20px;\n        font-size: 1.6em;\n        position: relative;\n        &:before {\n            height: 1px;\n            width: 100%;\n            background: #FFF;\n            position: absolute;\n            content: \" \";\n            bottom: 0;\n            left: 0;\n        }\n    }\n    .nothing-message {\n        height: 160px;\n        color: #666;\n        background-size: 15%;\n    }\n    li {\n        float: left;\n        width: 49%;\n        overflow: auto;\n        height: auto;\n        min-height: 10px;\n        background: #FFF;\n        display: inline-block;\n        padding: 20px;\n        border: 1px solid #CCC;\n        color: #666;\n        border-top: 9px solid $primary-color;\n        cursor: pointer;\n        margin-bottom: 20px;\n        margin-right: 2%;\n        transition: all 0.3s;\n        position: relative;\n        &:nth-child(even) {\n            margin-r