tent: \"\\eb28\";\n  font-family: 'icofont';\n  font-style: normal;\n}\n\n.datatable-icon-prev:before {\n  content: \"\\eb39\";\n  font-family: 'icofont';\n  font-style: normal;\n}\n\n.dt-desc {\n  th, td {\n    padding: .75rem;\n  }\n  padding: 0.45rem 50px;\n  table {\n    min-width: 400px;\n  }\n}\n\n.datatable-row-detail {\n  width: 100vw;\n  background-color: #f9f9f9;\n  box-shadow: inset 0px 2px 9px -6px rgba(0, 0, 0, 0.68);\n}\n\n.expandable {\n  datatable-body-cell + datatable-body-cell, datatable-header-cell + datatable-header-cell {\n    width: calc(calc(100% - 50px) / 5) !important;\n  }\n  .datatable-body-row, .datatable-header-inner {\n    width: 100% !important;\n  }\n}\n\n.datatable-row-center, .datatable-scroll, .data-table.expandable datatable-scroller {\n  width: 100% !important;\n}\n\n.ngx-datatable.data-table {\n  &.single-selection .datatable-body-row.active:hover .datatable-row-group, &.multi-selection .datatable-body-row.active:hover .datatable-row-group, &.single-selection .datatable-body-row.active .datatable-row-group, &.multi-selection .datatable-body-row.active .datatable-row-group {\n    background: $color-blue !important;\n    color: #FFF !important;\n  }\n}\n.ngx-datatable.fixed-row .datatable-scroll .datatable-body-row .datatable-body-cell{\n  overflow: visible !important;\n}\n.selection-cell .datatable-body-cell.active {\n  background: $color-blue !important;\n  color: #FFF !important;\n}\n\n.selected-column label {\n  margin-bottom: 5px;\n}\n\n.dt-chk {\n  label {\n    margin-bottom: 0;\n  }\n  .datatable-body-cell {\n    line-height: 1 !important;\n    overflow: hidden;\n  }\n}\n\n.full-data-search {\n  margin-left: 0.5em;\n  display: inline-block;\n  width: auto !important;\n}\n\n.full-data-show-entry {\n  width: 75px;\n  display: inline-block;\n}\n\n.full-data-table {\n  .glyphicon {\n    font-family: IcoFont;\n    position: absolute;\n    right: 14px;\n    font-size: 20px;\n    top: 9px;\n    &.glyphicon-triangle-bottom:before {\n      content: \"\\eb53\";\n    }\n    &.glyphicon-triangle-top:before {\n      content: \"\\eb56\";\n    }\n  }\n  th {\n    position: relative;\n  }\n}\n\n\n.pcoded[layout-type=\"dark\"], body.dark {\n  .data-table datatable-footer {\n    background-color: transparent;\n  }\n  .datatable-footer .datatable-pager a {\n    color: $light-color-text;\n  }\n  .datatable-row-detail {\n    background-color: transparent;\n  }\n}\n","\n// ng bootstrap accordion\n.ngb-accordion{\n  .card{\n    border-bottom:none;\n    border-top:1px solid rgba(0, 0, 0, 0.125);\n    box-shadow: none;\n    margin-bottom: 0;\n    border-radius: 0;\n    .card-header{\n      border-radius: 0;\n      border-bottom: none;\n      padding: 14px 20px;\n      box-shadow: none;\n      a{\n        transition: all 0.5s ease-in-out;\n        font-weight: 500;\n      }\n      &:hover,&:focus{\n        a{\n          color: $color-blue;\n          background: #fff;\n          &:hover,&:focus{\n            background: #fff;\n          }\n        }\n      }\n    }\n  }\n  &.color-accordion {\n    .card{\n      border-top:1px solid #fff;\n      .card-header{\n        transition: all 0s ease-in-out;\n        a{\n          transition: all 0s ease-in-out;\n        }\n        &:hover,&:focus,&.active{\n          background: $color-blue;\n          a{\n            color: #fff;\n            background: $color-blue;\n            &:hover,&:focus{\n              color: #fff;\n              background: $color-blue;\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n.pcoded[layout-type=\"dark\"], body.dark {\n  .ngb-accordion .card .card-header:hover a, .ngb-accordion .card .card-header:focus a {\n    background-color: transparent !important;\n  }\n\n  .ngb-accordion.color-accordion {\n    .card {\n      border-top: inherit;\n    }\n  }\n}\n","$header-height: 3.5rem !default;\n$header-footer-height: ($header-height * 2) !default;\n$enable-rounded: true !default;\n$border-radius: .125rem !default;\n$border-radius-lg: .25rem !default;\n$border-radius-sm: .0625rem !default;\n$font-family-headers: 'Montserrat', Helvetica, sans-serif !default;\n\n$card-border-radius: .125rem !default;\n$card-spacer-y:            1rem !default;\n$card-border-width:        0 !default;\n$card-border-color:        rgba($black,.06) !default;\n$card-cap-bg:              white !default;\n\n.taskboard {\n  overflow-x: auto;\n  overflow-y: hidden;\n  white-space: nowrap;\n  height: calc(100vh - #{$header-height});\n  padding: $spacer;\n  .taskboard-wrapper {\n    width: 280px;\n    padding-right: ($spacer / 2);\n    padding-left: ($spacer / 2);\n    box-sizing: border-box;\n    display: inline-block;\n    vertical-align: top;\n    height: 100%;\n    &:first-child {\n      padding-left: 0;\n    }\n    &:last-child {\n      padding-right: 0;\n    }\n  }\n  .taskboard-list {\n    box-sizing: border-box;\n    display: flex;\n    flex-direction: column;\n    max-height: 100%;\n    white-space: normal;\n    background-color: #fff;\n    border: 1px solid $border-color;\n    @if $enable-rounded {\n      border-radius: $border-radius;\n    } @else {\n      border-radius: 0;\n    }\n  }\n  .taskboard-header {\n    position: relative;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    font-family: $font-family-headers;\n    text-transform: uppercase;\n    letter-spacing: 0.02rem;\n    font-size: 13px;\n    padding: ($spacer/2) ($spacer/2) 0;\n  }\n  .taskboard-task {\n    position: relative;\n    display: flex;\n    flex-direction: column;\n\n    border: $card-border-width solid $card-border-color;\n    @include border-radius($card-border-radius);\n    padding: ($spacer/2);\n    margin-bottom: ($spacer/2);\n    &:last-child {\n      margin-bottom: 0;\n    }\n    &:hover {\n      cursor: grab;\n    }\n  }\n  .taskboard-task:after {\n    content: \"\";\n    position: absolute;\n    border: 4px solid transparent;\n    top: 0;\n    border-top-width: 12px;\n    border-bottom-color: transparent;\n    right: 6px;\n  }\n  .taskboard-task.task-status-success:after {\n    border-top-color: $success-color;\n    border-right-color: $success-color;\n    border-left-color: $success-color;\n  }\n  .taskboard-task.task-status-info:after {\n    border-top-color: $info-color;\n    border-right-color: $info-color;\n    border-left-color: $info-color;\n  }\n  .taskboard-task.task-status-warning:after {\n    border-top-color: $warning-color;\n    border-right-color: $warning-color;\n    border-left-color: $warning-color;\n  }\n  .taskboard-task.task-status-danger:after {\n    border-top-color: $danger-color;\n    border-right-color: $danger-color;\n    border-left-color: $danger-color;\n  }\n  .taskboard-cards {\n    padding: ($spacer/2);\n    box-sizing: border-box;\n    overflow-x: hidden;\n    overflow-y: auto;\n  }\n  .taskboard-task-title {\n    margin-bottom: ($spacer/2);\n  }\n}\n",".ripple {\n  position: relative;\n  overflow: hidden;\n  &:after {\n    content: \"\";\n    display: block;\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    background-image: radial-gradient(circle, #000 10%, transparent 10.01%);\n    background-repeat: no-repeat;\n    background-position: 50%;\n    transform: scale(10, 10);\n    opacity: 0;\n    transition: transform .5s, opacity 1s;\n  }\n  &:active:after {\n    transform: scale(0, 0);\n    opacity: .2;\n    transition: 0s;\n  }\n\n  &.menu-ripple {\n    width: 100%;\n    height: 100%;\n    position: absolute;\n    top: 0;\n    left: 0;\n    z-index: 1030;\n  }\n\n  &.light:after{\n    background-image: radial-gradient(circle, #fff 10%, transparent 10.01%);\n  }\n}\n",".ag-slider .noUi-horizontal {\n  height: 16px;\n  background: transparent;\n  border-radius: 0px;\n  border: none;\n  border-top: 1px solid #D3D3D3;\n  box-shadow: none;\n  cursor: pointer;\n  .noUi-handle {\n    width: 20px;\n    height: 20px;\n    top: -10px;\n    border: 1px solid transparent;\n    border-radius: 100%;\n    background-color: #4680ff;\n    box-shadow: none;\n    &:after, &:before {\n      background: transparent;\n    }\n  }\n  .noUi-connects {\n    padding: 10px;\n    top: -10px;\n    .noUi-connect {\n      background: #4680ff;\n      height: 3px;\n      top: 8px;\n    }\n  }\n\n  .noUi-pips-horizontal {\n    top: 0;\n    padding: 0;\n  }\n  .noUi-tooltip {\n    border: 1px solid $inverse-color;\n    border-radius: 5px;\n    background: $inverse-color;\n    color: #fff;\n    padding: 5px 15px;\n    transform: translate(-50%,-15px);\n    &:after{\n      content: \"\";\n      position: absolute;\n      border:6px solid transparent;\n      border-bottom-color: $inverse-color;\n      border-right-color: $inverse-color;\n      bottom: -10px;\n      left: 50%;\n      transform: rotate(45deg) translate(-50%,0);\n    }\n  }\n}\n","//=== wizard\n.ngx-form-wizard {\n  .nav-item{\n    a.nav-link {\n      background: #eee;\n      color: #aaa;\n      margin: 0 0.5em 0.5em;\n      padding: 1em 1em;\n      border-radius: 5px;\n      &.active{\n        background-color: $primary-color;\n        color: #fff;\n      }\n    }\n  }\n  .nav-tabs{\n    border-bottom: none;\n  }\n  .tab-pane{\n    border: 1px solid $theme-border;\n    padding: 20px;\n    margin: 8px;\n    border-radius: 5px;\n  }\n}\n.arc-wizard {\n  &.arc-custom {\n    .steps-indicator li:after {\n      border-radius: 5px !important;\n    }\n  }\n  .centered-content {\n    text-align: center;\n  }\n  .wizard-steps{\n    border-radius: 5px;\n    border:1px solid $theme-border;\n    padding: 20px;\n  }\n\n  .small ul.steps-indicator li.current:after,\n  .small ul.steps-indicator li.editing:after {\n    background-color: $primary-color !important;\n  }\n\n  .large-filled-symbols ul.steps-indicator li.current:after,\n  .large-filled-symbols ul.steps-indicator li.editing:after {\n    background-color: $primary-color !important;\n    color: #fff !important;\n  }\n\n  .large-filled-symbols ul.steps-indicator li.done:after {\n    color: #fff !important;\n  }\n\n  .large-empty-symbols ul.steps-indicator li.current:after,\n  .large-empty-symbols ul.steps-indicator li.editing:after {\n    border-color: $primary-color !important;\n    color: $primary-color !important;\n  }\n}\n",".error-400, .error-403, .error-404, .error-500, .error-503 {\n  position: fixed;\n  height: 100%;\n  width: 100%;\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-align: center;\n  -ms-flex-align: center;\n  align-items: center;\n  text-align: center;\n  background-color: #fff;\n}\n\n.error-400 h1, .error-403 h1, .error-404 h1, .error-500 h1, .error-503 h1 {\n  padding: 0;\n  margin-bottom: 40px;\n  font-size: 210px;\n  font-weight: 900;\n  line-height: 210px;\n  color: $inverse-color;\n}\n\n.error-400 h5, .error-403 h5, .error-404 h5, .error-500 h5, .error-503 h5 {\n  font-size: 24px;\n  margin-bottom: 20px;\n  font-weight: 400;\n  color: $primary-color;\n}\n\n.coming {\n  background: transparent url(\"./../../../assets/images/master-home.jpg\") no-repeat;\n  background-size: cover;\n  position: fixed;\n  height: 100%;\n  width: 100%;\n  display: -webkit-box;\n  display: -ms-flexbox;\n  display: flex;\n  -webkit-box-align: center;\n  -ms-flex-align: center;\n  align-items: center;\n}\n\n.comming-soon {\n  text-align: center;\n  color: #fff;\n}\n\n.comming-soon h1 {\n  font-size: 54px;\n  font-weight: 600;\n}\n\n.comming-soon p {\n  font-size: 24px;\n  font-weight: 400;\n  color: #fff;\n}\n\n.coming-social {\n  padding-top: 50px;\n  text-align: center;\n}\n\n.comming-soon .timer .time {\n  font-size: 90px;\n  color: #fff;\n}\n\n.comming-soon .timer .text {\n  font-size: 24px;\n  font-weight: 400;\n  color: $primary-color;\n}\n\n.coming-social a {\n  margin-right: 20px;\n  color: #fff;\n  font-size: 30px;\n}\n\n.mb70 {\n  margin-bottom: 70px;\n}\n\n.mb50 {\n  margin-bottom: 50px;\n}\n",".arc-wizard {\n  &.arc-custom {\n    .steps-indicator li:after {\n      border-radius: 5px !important;\n    }\n  }\n  .centered-content {\n    text-align: center;\n  }\n  .wizard-steps{\n    border-radius: 5px;\n    border:1px solid $theme-border;\n    padding: 20px;\n  }\n\n  ul.steps-indicator li:after {\n    font-weight: 900;\n  }\n\n  .small ul.steps-indicator li.current:after,\n  .small ul.steps-indicator li.editing:after {\n    background-color: $primary-color !important;\n  }\n\n  .large-filled-symbols ul.steps-indicator li.current:after,\n  .large-filled-symbols ul.steps-indicator li.editing:after {\n    background-color: $primary-color !important;\n    color: #fff !important;\n\n  }\n\n  .large-filled-symbols ul.steps-indicator li.done:after {\n    color: #fff !important;\n  }\n\n  .large-empty-symbols ul.steps-indicator li.current:after,\n  .large-empty-symbols ul.steps-indicator li.editing:after {\n    border-color: $primary-color !important;\n    color: $primary-color !important;\n  }\n\n  aw-wizard-navigation-bar.horizontal.large-filled-symbols ul.steps-indicator li .step-indicator {\n    color: #fff;\n    font-size: 24px;\n  }\n\n  aw-wizard-navigation-bar.horizontal.large-empty-symbols ul.steps-indicator li .step-indicator {\n    font-size: 24px;\n  }\n}\n","/**  =====================\n      Data Tables css start\n==========================  **/\n.table {\n    td,\n    th {\n        border-top: 1px solid $theme-border;\n        white-space: nowrap;\n        padding: 0.5rem 0.75rem;//aditya td padding\n    }\n\n    thead th {\n        border-bottom: 1px solid $theme-border;\n        font-size: 13px;\n        color: $headings-color;\n        background: $theme-bg-color;\n        text-transform: uppercase;\n    }\n\n    tbody + tbody {\n        border-top: 2px solid $theme-border;\n    }\n\n    &.table-dark {\n        thead th {\n            background-color: #11171a;\n        }\n    }\n\n    &.dataTable[class*=\"table-\"] {\n        thead th {\n            background-color: $theme-bg-color;\n        }\n    }\n}\n/* Bo